import classStuff from '../models/class';
import help from '../helpers/helper';

const XController = {
  insert (req, res) {
    const{Name, Marks, Level, Email} = req.body;
    const ckName = help.check_name_if_is_empty(Name);
    if (ckName) return res.status(400).send({'message': 'Hey Check Name field are required'});

    const ckStrngName = help.check_length_name(Name);
    if (ckStrngName) return res.status(400).send({'message': 'Hey Name should contain above six characters'});

    const ckStrngName1 = help.check_if_name_contains_number(Name);
    if (ckStrngName1) return res.status(400).send({'message': 'Hey Name should contain numbers'});

    const ckStrngName2 = help.check_if_name_contains_character(Name);
    if (ckStrngName2) return res.status(400).send({'message': 'Hey Name should contain special character like qwerty@123'});
   
    const cksample = help.check_if_the_use_sample(Name);
    if (cksample) return res.status(400).send({'message': 'Hey no one allowed to usethis sample qwerty@123'});
  
    const ckMarks = help.check_mark_if_is_empty(Marks);
    if (ckMarks) return res.status(400).send({'message': 'Hey Check Marks field is required'});

    const ckLevel = help.check_Level_if_is_empty(Level);
    if (ckLevel) return res.status(400).send({'message': 'Hey Check Level field is required'});

    const ckEmail = help.check_Email_if_is_empty(Email);
    if (ckEmail) return res.status(400).send({'message': 'Hey Check Email field is required'});

    const ckEmaila = help.check_email(Email);
    if (ckEmaila) return res.status(400).send({'message': 'Hey you are using Invalid Email'});

    const datas = {
      Name :Name.toString().trim().toLowerCase(),
      marks :Marks.toString().trim().toLowerCase(),
      Marks :Level.toString().trim().toLowerCase(),
      Email :Email.toString().trim().toLowerCase()
    }

    classStuff.create(datas);
    return res.status(201).send({message : `Hy You added Successfully `, datas});
    

  },
  retrieve (req, res) {
    const students = classStuff.findAll();
    return res.status(200).send({'message': 'student retrieved Successfully', students});
  },
  retrieveOne : (req, res) => {
    const student = classStuff.findOne( parseInt(req.params.id));

    if (!student) {
      return res.status(404).send({'message': 'student not found'});
    }
    return res.status(200).send({'message': 'student retrieved Successfully', student});
  },
  update : (req, res) => {
    const student = classStuff.findOne(parseInt(req.params.id));
    if (!student) {
      return res.status(404).send({'message': 'student not found'});
    }
    const updatedclassStuff = classStuff.update(parseInt(req.params.id), req.body)
    return res.status(200).send({'message': 'student updated Successfully',updatedclassStuff});
  },
  delete : (req, res) => {
    const student = classStuff.findOne(parseInt(req.params.id));
    if (!student) {
      return res.status(404).send({'message': 'student not found'});
    }
    const stud = classStuff.delete(parseInt(req.params.id));
    return res.status(204).send({'message': 'student deleted Successfully', Data:stud});
  }
}
export default XController;